<template>
    <v-container>
        <v-row class="px-2">
            <v-col cosl="12" sm="6" md="6" v-for="item of items" :key="item.id">
                <v-card class="mx-auto elevation-1" max-width="400" outlined>
                    <v-list-item three-line>

                    <v-list-item-content>
                        <div class="overline mb-4">Digital Nets</div>
                        <v-list-item-title class="headline mb-1">{{ item.title }}</v-list-item-title>
                    </v-list-item-content>
                    </v-list-item>

                    <v-dialog v-model="item.dialog" max-width="600">
                    <v-card>
                        <v-card-title class="headline"> {{item.title}} </v-card-title>

                        <v-card-text>
                            <div class="m-3">
                                {{ item.description }}
                            </div>
                            <div class="m-3">
                                Progreso
                                <div class="m-1">
                                    <v-progress-linear color="primary" height="13" rounded v-model="item.percentage">
                                        {{ item.percentage }}%
                                    </v-progress-linear>
                                </div>
                                <div class="m-1">
                                    costo: {{ item.price }}$
                                </div>
                                <div class="m-1">
                                    <v-tooltip right>
                                    <template v-slot:activator="{ on }">
                                        <span v-on="on">
                                            Profesionales Asignados: {{ item.professionalsCount }}
                                        </span>
                                    </template>
                                    <span v-for="pro of item.professionals" :key="pro.id">
                                        <div>
                                            <p>
                                                {{ pro.name}} - {{ pro.profesion }} - {{ pro.email }}
                                            </p>
                                        </div>
                                    </span>
                                    </v-tooltip>
                                </div>
                                <div class="m-1">
                                    Cliente asignado: {{ item.client.name }} - {{ item.client.email }}
                                </div>
                            </div>
                        </v-card-text>

                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn text color="primary" @click="item.dialog2 = !item.dialog2">Ver Actividades</v-btn>
                            <v-btn text color="primary" @click="item.dialog = false">Salir</v-btn>
                        </v-card-actions>
                    </v-card>
                    </v-dialog>

                    <v-dialog v-model="item.dialog2" max-width="400">
                    <v-card>
                        <v-card-title class="headline">Actividades</v-card-title>

                        <v-card-text>
                            <div v-for="act of item.activitys" :key="act.id">
                                <h6> {{ act.title }} </h6>
                                <p>
                                    {{ act.description }}
                                </p>
                                <p>
                                    porcentaje representativo:  {{ act.percentage }} %
                                </p>
                                <p v-if="act.state === false">
                                    Estado: En progreso
                                </p>
                                <p v-if="act.state === true">
                                    Estado: Finalizada
                                </p>
                                <v-divider></v-divider>
                            </div>
                        </v-card-text>

                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn text color="primary" @click="item.dialog2 = false">Salir</v-btn>
                        </v-card-actions>
                    </v-card>
                    </v-dialog>

                    <v-card-actions>
                    <v-btn text color="primary" @click="item.dialog = !item.dialog">Detalles</v-btn>
                    <v-btn text color="primary" @click="elimPro(item)" v-if="item.finish === false">Cancelar Proyecto</v-btn>
                    <v-btn text color="primary" @click="DeletePro(item)" v-if="item.finish === true">Borrar Proyecto Finalizado</v-btn>
                    </v-card-actions>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
import { mapMutations } from 'vuex';
export default {
    name: 'ViewProyect',
    data() {
        return {
            dialog: false,
            dialog2: false,
            items: [],
        }
    },
    created() {
        this.axios.get('/proyects')
            .then(res => {
                let proyectsArray = res.data.data;
                proyectsArray.forEach(element => {
                    this.charge(element)
                });
            })
            .catch(err => {
                console.log(err.response)
            })
    },
    methods: {
        ...mapMutations(['clientsActLess', 'professionalActLess']),
        charge(element){
            this.items.push({
                title: element.title,
                description: element.description,
                percentage: element.percentage,
                finish: element.finish,
                price: element.price,
                client: {
                    name: element.client.name,
                    email: element.client.email
                },
                professionals: element.professionals,
                activitys: element.activities,
                clientsCount: element.clientCount,
                professionalsCount: element.professionalCount,
                activitysCount: element.activitysCount,
                id: element._id,
                dialog: false,
                dialog2: false,
            })
        },
        elimPro(item){
             if(confirm('¿Estas seguro de eliminar este Proyecto?')){
                let pro = item.professionals;
                pro.forEach(element => {
                    let indexPro = element.proyects.indexOf(item.id)
                    element.proyects.splice(indexPro,1)
                    if(element.proyects.length === 0){
                        this.axios.put(`professionals/${element.email}`,{
                            name: element.name,
                            lastName: element.lastName,
                            email: element.email,
                            profesion: element.profesion,
                            role: element.role,
                            asig: false,
                            proyects: element.proyects
                        })
                            .then(res => {
                                this.axios.delete(`proyects/${item.id}`)
                                    .then(res => {
                                        let index = this.items.indexOf(item)
                                        this.items.splice(index, 1);
                                        this.clientsActLess()
                                        this.UpdateClient(item)
                                    })
                                    .catch(err => {
                                        console.log(err.response)
                                    })
                            })
                            .catch(err => {
                                console.log(err.response)
                            })
                        this.professionalActLess();
                    }else{
                        this.axios.put(`professionals/${element.email}`,{
                            name: element.name,
                            lastName: element.lastName,
                            email: element.email,
                            profesion: element.profesion,
                            role: element.role,
                            asig: true,
                            proyects: element.proyects
                        })
                        .then(res => {
                            this.axios.delete(`proyects/${item.id}`)
                                .then(res => {
                                    let index = this.items.indexOf(item)
                                    this.items.splice(index, 1);
                                    this.clientsActLess()
                                    this.UpdateClient(item)
                                })
                                .catch(err => {
                                    console.log(err.response)
                                })
                        })
                        .catch(err => {
                            console.log(err.response)
                        })
                    }
                });
            }
        },
        DeletePro(item){
            if(confirm('¿Estas seguro de borrar este Proyecto?')){
                this.axios.delete(`proyects/${item.id}`)
                    .then(res => {
                        let index = this.items.indexOf(item)
                        this.items.splice(index, 1);
                        this.clientsActLess()
                        this.UpdateClient(item)
                    })
                    .catch(err => {
                        console.log(err.response)
                    })
            }
        },
        UpdateClient(item){
            this.axios.get('/clients')
                .then(res => {
                    res.data.clients.forEach(element => {
                        if(element.email === item.client.email){
                            element.asig = false;
                            this.axios.put(`clients/${element.email}`,element)
                                .then(re => {
                                    console.log('exito')
                                })
                                .catch(err => {
                                    console.log(err.response)
                                })
                        }
                    });
                })
                .catch(err => {
                    console.log(err.response)
                })
        },
    },
}
</script>