<template>
  <v-data-table :headers="headers" :search="search" :items="professionals" sort-by="calories" class="elevation-1">
    <template v-slot:top>
      <v-toolbar flat color="white">

        <v-toolbar-title>
          <span> Profesionales</span>
        </v-toolbar-title>

        <v-divider class="mx-4" inset vertical></v-divider>

        <v-alert dense text type="success" v-if="alertState === true">
          {{ alertMsg }}
        </v-alert>

        <v-alert dense text type="error" v-if="alertStateE === true">
          {{ alertMsg }}
        </v-alert>

        <v-spacer></v-spacer>
      
        <v-text-field class="text-xs-center" v-model="search" append-icon="search" label="busqueda" single-line hide-details></v-text-field>
        <v-spacer></v-spacer>
        <v-dialog v-model="dialog" max-width="500px">

          <template v-slot:activator="{ on }">
            <v-btn color="primary" dark class="mb-2" v-on="on">Agregar Profesional</v-btn>
          </template>
          <v-card>

            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.name" label="Nombre"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.lastName" label="Apellido"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.email" label="E-Mail"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.profesion" label="profesion"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.password" label="password"></v-text-field>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
              <v-btn color="blue darken-1" text @click="save">Save</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>

      </v-toolbar>

    </template>

    <template v-slot:item.action="{ item }">
      <v-icon small class="mr-2" @click="editItem(item)">edit</v-icon>
      <v-icon small @click="deleteItem(item)" >delete
      </v-icon>
    </template>

  </v-data-table>
</template>


<script>
import { mapMutations } from 'vuex';
  export default {
    name: 'Clients',
    data: () => ({
      dialog: false,
      headers: [
        {
          text: 'Nombre',
          align: 'left',
          sortable: false,
          value: 'name',
        },
        { text: 'Apellido', value: 'lastName' },
        { text: 'E-Mail', value: 'email' },
        { text: 'Role', value: 'role' },
        { text: 'Profesion', value: 'profesion' },
        { text: 'Acciones', value: 'action', sortable: false },
      ],
      professionals: [],
      editedIndex: -1,
      alertState: false,
      alertStateE: false,
      alertMsg: '',
      search: '',
      editedItem: {
        name: '',
        lastName: '',
        email: '',
        profesion: '',
        password: '',
        role: 'user',
      },
      defaultItem: {
        name: '',
        lastName: '',
        email: '',
        profesion: '',
        password: '',
        role: 'user',
      },
    }),

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'Agregar Profesional' : 'Editar Profesional'
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
      alertState: function(){
        setTimeout(this.alertStateF, 3000);
      },
      alertStateE: function(){
        setTimeout(this.alertStateError, 3000);
      },
    },

    created () {
      this.initialize();
    },

    methods: {
      ...mapMutations(['professionalWatchPlus', 'professionalWatchLess']),
      alertStateF(){
        this.alertState = false;
      },
      alertStateError(){
        this.alertStateE = false;
      },
      initialize () {
        this.axios.get('/professionals')
          .then(res => {
            let professionalsArray = res.data.users;
            professionalsArray.forEach(element => {
              this.charge(element);
            });
          })
          .catch(err => {
            console.log(err.response)
          })
      },
      charge(element){
        this.professionals.push(element);

      },
      editItem (item) {
        item.password = "";
        this.editedIndex = this.professionals.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        const index = this.professionals.indexOf(item)
        if(confirm('¿Estas seguro de eliminar este cliente?')){
          if(item.role === 'admin'){
            this.alertStateE = true;
            this.alertMsg = 'El administrador no puede ser eliminado mediante esta interfaz';
            return false
          }
          if(item.asig === true){
            this.alertStateE = true;
            this.alertMsg = 'Este profesional posee un proyecto asginado, debe cancelar el proyecto o finalizarlo';
            return false
          }else{
            this.axios.delete(`professionals/${item.email}`)
              .then(res => {
                this.professionals.splice(index, 1);
                this.alertState = true;
                this.alertMsg = res.data.msg;
                this.professionalWatchLess();
              })
              .catch(err => {
                this.alertStateE = true;
                this.alertMsg = 'Ocurrio un error inesperado';
              })
          }
        }
      },

      close () {
        this.dialog = false
        setTimeout(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        }, 300)
      },
      save () {
        if (this.editedIndex > -1) {
          let fieldsValidate = this.validate(this.editedItem);
          if(fieldsValidate === true){
            let email = this.editedItem.email;
            this.axios.put(`professionals/${this.editedItem.email}`,{
              name: this.editedItem.name,
              lastName: this.editedItem.lastName,
              email: this.editedItem.email,
              profesion: this.editedItem.profesion,
              role: this.editedItem.role
            })
              .then(res => {
                Object.assign(this.professionals[this.editedIndex], this.editedItem);
                this.alertState = true;
                this.alertMsg = res.data.msg;
              })
              .catch(err => {
                this.alertStateE = true;
                this.alertMsg = 'No puedes modificar el E-Mail de contacto';
              })
          }else{
            this.alertStateE = true;
            this.alertMsg = 'Los campos no pueden estar vacios';
            this.close;
          }
        } else {
          let validateFields = this.validate(this.editedItem);
          if(validateFields === true){
            this.axios.post('/signin',{
              name: this.editedItem.name,
              lastName: this.editedItem.lastName,
              email: this.editedItem.email,
              profesion: this.editedItem.profesion,
              password: this.editedItem.password,
              role: this.editedItem.role,
            })
              .then(res => {
                this.professionals.push(this.editedItem);
                this.alertState = true;
                this.alertMsg = res.data.msg;
                this.professionalWatchPlus();
              })
              .catch(err => {
                this.alertStateE = true;
                this.alertMsg = 'Por favor ingrese un E-Mail Valido';
              })
            this.close();
          }else{
            this.alertStateE = true;
            this.alertMsg = 'Todos los campos son obigatorios';
          }
        }
      },
      validate(item){
        let name = item.name.replace(/ /g, "");
        let lastName = item.lastName.replace(/ /g, "");
        let email = item.email.replace(/ /g, "");
        let profesion = item.profesion.replace(/ /g, "");
        let password = item.password.replace(/ /g, "");
        let role = item.role.replace(/ /g, "");
        if(name === "" || lastName === "" || email === "" || profesion === "" || role === "" || password === ""){
          return false;
        }else{
          return true;
        }
      },
    },
  }
</script>