<template>
  <v-app>
    <v-navigation-drawer v-model="drawer" app >

      <v-container fluid class="header">
        <span>Digital Nets</span>
      </v-container>

       <v-list-item v-for="item of items" :key="item.id">
          <v-btn text color="primary" block @click="itemState(item)">
            <v-icon class="mx-3"> {{ item.icon }} </v-icon>  {{ item.title }}
          </v-btn>
       </v-list-item>

    </v-navigation-drawer>

    <v-app-bar app light  class="elevation-2">
      <v-app-bar-nav-icon @click.stop="drawer = !drawer" />
      <v-toolbar-title> {{ userName}} </v-toolbar-title>
    </v-app-bar>

    <v-content class="content">

      <transition name="slide-fade">
        <v-container class="m-3" v-if="dash === true">
          <DashboardU></DashboardU>
        </v-container>
      </transition>

    </v-content>

    <v-footer class="footer" app>
      <span>&copy; 2020 Victor Naranjo</span>
    </v-footer>

  </v-app>
</template>

<style scoped>
  .footer{
    background: #fff !important;
  }
  .header{
    background: #3A539B !important;
    height: 66px;
    display: grid;
    align-content: center;
    justify-content: center;
    color: #fff;
  }
  .content{
    background: #ECF0F1 !important;
    margin: 0px !important;
    padding: 0px !important; 
  }
  .slide-fade-enter-active {
    transition: all .3s ease;
  }
  .slide-fade-leave-active {
    transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .slide-fade-enter, .slide-fade-leave-to{
    transform: translateX(10px);
    opacity: 0;
  }
</style>

<script>
import DashboardU from '../components/DashboardU'
  export default {
    beforeCreate() {
      if(!this.$session.exists()){
        this.$router.push('/')
      }
      let data = this.$session.getAll();
      if(data.true.email === 'admin@gmail.com'){
        this.$router.push('/dashboard')
      }
    },
    created() {
      let dataSesion = this.$session.getAll();
      this.userName = dataSesion.true.name;
    },
    components: {

        DashboardU
    },
    props: {
      source: String,
    },
    data: () => ({
      drawer: null,
      dash: false,
      userName: '',
      items: [
        {
          icon: 'mdi-view-dashboard', title: 'Dashboard'
        },
        {
          icon: 'mdi-arrow-up-bold-box-outline', title: 'Cerrar Sesion'
        },
      ]
    }),
    methods: {
      itemState(item){
        if(item.title == 'Dashboard'){
          this.dash = !this.dash;
        }
        if(item.title == 'Cerrar Sesion'){
          this.$session.destroy()
          this.$router.push('/')
        }
      },
    }
  }
</script>