<template>
    <v-container class="">
        <v-row>

            <v-col cols="12" sm="6" md="4" class="">
                <v-card class="mx-auto text-white" min-width="344">
                    <v-card class="mx-auto text-white subcard" min-width="344" color="primary">
                        <v-card-text>
                            <p class="display-1 text-white">
                                Clientes
                            </p>
                        </v-card-text>
                    </v-card>
                    <v-card-text>
                        <div>Cantidad de clientes</div>
                        <p class="display-1 text--primary">
                            Clientes
                        </p>
                        <div v-for="item of  clientsState" :key="item.id">
                            Clientes activos: {{ clientAct }}%
                             <v-divider></v-divider>
                             Total: {{ item.total }}
                             <v-divider vertical></v-divider>
                             Inactivos: {{ clientInc }}%
                        </div>
                    </v-card-text>
                </v-card>
            </v-col>

            <v-col cols="12" sm="6" md="4" class="">
                <v-card class="mx-auto text-white" min-width="344">
                    <v-card class="mx-auto text-white subcard" min-width="344" color="primary">
                        <v-card-text>
                            <p class="display-1 text-white">
                                Profesionales
                            </p>
                        </v-card-text>
                    </v-card>
                    <v-card-text>
                        <div>Cantidad de Profesionales</div>
                        <p class="display-1 text--primary">
                            Profesionales
                        </p>
                        <div v-for="item of  professionalsState" :key="item.id">
                            Profesionales activos % : {{ proAct}}
                             <v-divider></v-divider>
                             Total: {{ item.total }}
                             <v-divider vertical></v-divider>
                             Inactivos: {{ proInc }}%
                        </div>
                    </v-card-text>
                </v-card>
            </v-col>
        </v-row>
        <v-container>
            <v-row>
                <v-col cols="12">
                    <div class="my-5">
                        <p class="text-muted">
                            Proyectos finalizados
                        </p>
                        <v-progress-linear color="teal" height="13" rounded v-model="proyectsFinB">
                            {{ proyectsFinB }}%
                        </v-progress-linear>
                    </div>
                    <div class="my-5">
                        <p class="text-muted">
                            Proyectos en curso
                        </p>
                        <v-progress-linear color="yellow" height="13" rounded v-model="proyectsProgressBar">
                            {{ proyectsProgressBar }}%
                        </v-progress-linear>
                    </div>
                    <div class="my-5">
                        <p class="text-muted">
                            Proyectos en espera
                        </p>
                        <v-progress-linear color="error" height="13" rounded v-model="proyectsSB">
                            {{ proyectsSB }}%
                        </v-progress-linear>
                    </div>
                </v-col>
            </v-row>
        </v-container>
    </v-container>
</template>
<style scoped>
    .subcard{
        position: absolute;
        left: 8px;
        top: -15px;
    }
</style>
<script>
import { mapState } from 'vuex';
export default {
    name: 'Dashboard',
    computed: {
        ...mapState(['clientsState', 'professionalsState'])
    },
    data() {
        return {
            clientAct: 0,
            clientInc: 0,
            proAct: 0,
            proInc: 0,
            proyectsFin: 0,
            proyectsFinB: 0,
            proyectsProgress: 0,
            proyectsProgressBar: 0,
            proyectsS: 0,
            proyectsSB: 0,
            totalP: 0,
        }
    },
    watch: {
        clientAct: function(){
            if(this.clientsState[0].total != 0){
                this.clientAct = Math.round(this.clientsState[0].activos * 100 / this.clientsState[0].total);
            }else{
                this.clientAct = 0;
            }
        },
        clientInc: function(){
            if(this.clientsState[0].total === 0){
                this.clientInc = 0;
            }else{
                let total = this.clientsState[0].total - this.clientsState[0].activos;
                this.clientInc = Math.round(total * 100 / this.clientsState[0].total);
            }
        },
        proInc: function(){
            if(this.professionalsState[0].total === 0){
                this.proInc = 0;
            }else{
                let totalPro = this.professionalsState[0].total - this.professionalsState[0].activos;
                this.proInc = Math.round(totalPro * 100 / this.professionalsState[0].total);
            }
        },
        proAct: function(){
            if(this.professionalsState[0].total === 0){
                this.proAct = 0;
            }else{
                this.proAct = Math.round(this.professionalsState[0].activos * 100 / this.professionalsState[0].total);
            }
        },
        proyectsProgress: function(){
            this.proyectsProgressBar = this.proyectsProgress * 100 / this.totalP;
        },
        proyectsFin: function(){
            this.proyectsFinB = this.proyectsFin * 100 / this.totalP;
        },
        proyectsS: function(){
            this.proyectsSB = this.proyectsS * 100 / this.totalP;
            console.log(this.totalP)
        }
    },
    created() {
        if(this.clientsState[0].total === 0){
            this.clientInc = 0;
        }else{
            let total = this.clientsState[0].total - this.clientsState[0].activos;
            this.clientInc = total * 100 / this.clientsState[0].total;
            if(this.clientsState[0].activos === 0){
                this.clientAct = 0;
            }else{
                this.clientAct = this.clientsState[0].activos * 100 / this.clientsState[0].total;
            }
        }
        if(this.professionalsState[0].total === 0){
            this.proInc = 0;
        }else{
            let totalPro = this.professionalsState[0].total - this.professionalsState[0].activos;
            this.proInc = totalPro * 100 / this.professionalsState[0].total;
            if(this.professionalsState[0].activos === 0){
                this.proAct = 0;
            }else{
                this.proAct = this.professionalsState[0].activos * 100 / this.professionalsState[0].total;
            }
        }
        this.axios.get('/proyects')
            .then(res => {
                res.data.data.forEach(element => {
                    this.totalP++;
                    if(element.percentage > 0 && element.percentage < 100){
                        this.proyectsProgress++;
                    }
                    if(element.percentage === 100){
                        this.proyectsFin++;
                    }
                    if(element.percentage === 0){
                        this.proyectsS++;
                    }
                });
            })
            .catch(err => {
                console.log(err.response)
            })
    },
}
</script>