<template>
  <v-app>
    <v-navigation-drawer v-model="drawer" app >

      <v-container fluid class="header">
        <span>Digital Nets</span>
      </v-container>

       <v-list-item v-for="item of items" :key="item.id">
          <v-btn text color="primary" block @click="itemState(item)">
            <v-icon class="mx-3"> {{ item.icon }} </v-icon>  {{ item.title }}
          </v-btn>
       </v-list-item>

    </v-navigation-drawer>

    <v-app-bar app light  class="elevation-2">
      <v-app-bar-nav-icon @click.stop="drawer = !drawer" />
      <v-toolbar-title>Aplicación</v-toolbar-title>
    </v-app-bar>

    <v-content class="content">

      <transition name="slide-fade">
        <v-container class=" m-3" v-if="dashboard === true">
          <Dashboard></Dashboard>
        </v-container>
      </transition>

      <transition name="slide-fade">
        <v-container class="m-3" v-if="clients === true">
          <Clients></Clients>
        </v-container>
      </transition>

      <transition name="slide-fade">
        <v-container class="m-3" v-if="pro === true">
          <Profesional></Profesional>
        </v-container>
      </transition>

      <transition name="slide-fade">
        <v-container class="m-3" v-if="proyectos === true">
          <ProyectosD></ProyectosD>
        </v-container>
      </transition>

      <transition name="slide-fade">
        <v-container class="m-3" v-if="proyectosV === true">
          <ViewProyect></ViewProyect>
        </v-container>
      </transition>

    </v-content>

    <v-footer class="footer" app>
      <span>&copy; 2020 Victor Naranjo</span>
    </v-footer>

  </v-app>
</template>

<style scoped>
  .footer{
    background: #fff !important;
  }
  .header{
    background: #3A539B !important;
    height: 66px;
    display: grid;
    align-content: center;
    justify-content: center;
    color: #fff;
  }
  .content{
    background: #ECF0F1 !important;
    margin: 0px !important;
    padding: 0px !important; 
  }
  .slide-fade-enter-active {
    transition: all .3s ease;
  }
  .slide-fade-leave-active {
    transition: all .3s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .slide-fade-enter, .slide-fade-leave-to{
    transform: translateX(10px);
    opacity: 0;
  }
</style>

<script>
import { mapState, mapMutations } from 'vuex';
  import Clients from '../components/Clientes';
  import Dashboard from '../components/DashboardC';
  import Profesional from '../components/Profesionales';
  import ProyectosD from '../components/ProyectosD';
  import ViewProyect from '../components/ViewProyects';
  export default {
    beforeCreate() {
      if(!this.$session.exists()){
        this.$router.push('/')
      }
      let data = this.$session.getAll();
      if(!data.true.email === 'admin@gmail.com'){
        this.$router.push('/dashboardUser')
      }
    },
    components: {
      Clients,
      Dashboard,
      Profesional,
      ProyectosD,
      ViewProyect
    },
    props: {
      source: String,
    },
    data: () => ({
      drawer: null,
      dashboard: false,
      clients: false,
      pro: false,
      proyectos: false,
      proyectosV: false,
      items: [
        {
          icon: 'mdi-view-dashboard', title: 'Dashboard'
        },
        {
          icon: 'mdi-account-group-outline', title: 'Clientes'
        },
        {
          icon: 'mdi-account-group-outline', title: 'Profesionales'
        },
        {
          icon: 'folder_open', title: 'Definir Proyecto'
        },
        {
          icon: 'folder_open', title: 'Ver Proyectos'
        },
        {
          icon: 'mdi-arrow-up-bold-box-outline', title: 'Cerrar Sesion'
        }
      ]
    }),
    computed: {
      ...mapState(['clientsState', 'professionalsState'])
    },
    methods: {
      itemState(item){
        if(item.title == 'Dashboard'){
          this.dashboard = !this.dashboard;
          this.clients = false;
          this.pro = false
          this.proyectos = false;
          this.proyectosV = false;
        }
        if(item.title == 'Clientes'){
          this.clients = !this.clients;
          this.dashboard = false;
          this.pro = false;
          this.proyectos = false;
          this.proyectosV = false;
        }
        if(item.title == 'Profesionales'){
          this.pro = !this.pro;
          this.clients = false;
          this.dashboard = false;
          this.proyectos = false;
          this.proyectosV = false;
        }
        if(item.title == 'Definir Proyecto'){
          this.proyectos = !this.proyectos;
          this.clients = false;
          this.dashboard = false;
          this.pro = false;
          this.proyectosV = false;
        }
        if(item.title == 'Ver Proyectos'){
          this.proyectosV = !this.proyectosV;
          this.clients = false;
          this.dashboard = false;
          this.proyectos = false;
          this.pro = false;
        }
        if(item.title == 'Cerrar Sesion'){
          this.$session.destroy()
          this.$router.push('/')
        }
      },
      ...mapMutations(['clientsWatchPlus', 'professionalWatchPlus', 'clientsAct', 'clientsActInit', 'clientsWatchInit', 'professionalAct', 'professionalWatchInit', 'professionalActInit'])
    },
    created() {
        this.axios.get('/clients')
          .then(res => {
            this.clientsWatchInit();
            let ClientsArray = res.data.clients;
            ClientsArray.forEach(element => {
              this.clientsWatchPlus();
            });
          })
          .catch(err => {
            console.log(err.response)
          })
          this.axios.get('/professionals')
          .then(res => {
            this.professionalWatchInit();
            this.professionalActInit();
            let professionalsArray = res.data.users;
            professionalsArray.forEach(element => {
              if(element.asig === true){
                this.professionalAct();
              }
             this.professionalWatchPlus();
            });
          })
          .catch(err => {
            console.log('error')
          })
        this.axios.get('/proyects')
            .then(res => {
              this.clientsActInit();
                let proyectsArray = res.data.data;
                proyectsArray.forEach(element => {
                    this.clientsAct()
                });
            })
            .catch(err => {
                console.log(err.response)
            })
    },
  }
</script>