<template>
  <v-app id="inspire">
    <v-content>
      <v-container class="fill-height" fluid >
        <v-row align="center" justify="center">
          <v-col cols="12" sm="8" md="4"
          >
            <v-card class="elevation-12">
              <v-toolbar color="primary" dark flat>
                <v-toolbar-title>Login</v-toolbar-title>
                <v-spacer />
              </v-toolbar>
              <v-card-text>
                <v-alert dense border="left" type="error" v-if="error === true"> {{errorMsg}} </v-alert>
                <v-form>
                  <v-text-field label="Login" name="login" prepend-icon="person" v-model="email" type="text" />

                  <v-text-field id="password" label="Password" name="password" prepend-icon="lock" v-model="password" type="password"/>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-spacer />
                <v-tooltip top>
                <template v-slot:activator="{ on }">
                    <v-btn color="primary" dark v-on="on" @click="validate()">login</v-btn>
                </template>
                <span>
                    Para acceder como administrador use la siguientes credenciales:
                    E-Mail: admin@gmail.com, password: 1234
                </span>
                </v-tooltip>
              </v-card-actions>
            </v-card>
          </v-col>
        </v-row>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
  export default {
      beforeCreate() {
        if(this.$session.exists()){
            this.$router.go(-1)
        }
      },
    props: {
      source: String,
    },
    data() {
        return {
            email: '',
            password: '',
            error: false,
            errorMsg: ''
        }
    },
    watch: {
        error: function(){
            setTimeout(()=>{
                this.error = false
            }, 2000)
        }
    },
    methods: {
        validate(){
            let email = this.email.replace(/ /g, "");
            let password = this.email.replace(/ /g, "")
            if(email === "" || password === ""){
                this.error = true;
                this.errorMsg = 'Los campos no pueden estar vacios'
            }else{
                this.login();
            }
        },
        login(){
            this.axios.post('/login', {
                email: this.email,
                password: this.password
            })
                .then(res => {
                    this.$session.start();
                    this.$session.set(res.data.auth,res.data.userData);
                    if(res.data.userData.email === 'admin@gmail.com'){
                        this.$router.push('/dashboard');
                    }else{
                        this.$router.push('/dashboardUser');
                    }
                })
                .catch(err => {
                    this.error = true;
                    this.errorMsg = err.response.data.msg;
                })
        }
    },
  }
</script>