import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    clientsState: [
      {
        total: 0,
        activos: 0,
        inactivos: 0
      }
    ],
    professionalsState: [
      {
        total: 0,
        activos: 0,
        inactivos: 0
      }
    ]
  },
  mutations: {
    clientsWatchPlus(state){
      state.clientsState[0].total ++;
    },
    clientsWatchInit(state){
      state.clientsState[0].total = 0;
    },
    clientsAct(state){
      state.clientsState[0].activos++;
    },
    clientsActInit(state){
      state.clientsState[0].activos = 0;
    },
    clientsActLess(state){
      state.clientsState[0].activos--;
    },
    clientsWatchLess(state){
      state.clientsState[0].total --;
    },
    professionalAct(state){
      state.professionalsState[0].activos++;
    },
    professionalActInit(state){
      state.professionalsState[0].activos = 0;
    },
    professionalActLess(state){
      state.professionalsState[0].activos--;
    },
    professionalWatchPlus(state){
      state.professionalsState[0].total ++;
    },
    professionalWatchInit(state){
      state.professionalsState[0].total = 0;
    },
    professionalWatchLess(state){
      state.professionalsState[0].total --;
    },
  },
  actions: {
  },
  modules: {
  }
})
