<template>
  <v-stepper v-model="e6" vertical>
    <div class="p-3">
        <v-alert dense text type="error" v-if="alert === true">
          {{ alertMsg }}
        </v-alert>
        <v-alert dense text type="success" v-if="alertS === true">
          {{ alertSM }}
        </v-alert>
    </div>
    <v-stepper-step :complete="e6 > 1" step="1">
      Definir proyecto
      <small>Titulo Y descripción del mismo</small>
    </v-stepper-step>

    <!--Paso 1 Titulo y descripcion-->
    <v-stepper-content step="1">
      <v-card color="" class="mb-12 px-5 " height="100%">

        <v-text-field label="Titulo" v-model="items1.title"></v-text-field>
        <v-text-field label="Precio" v-model="items1.price"></v-text-field>
        <v-textarea label="Descripcion" v-model="items1.description"></v-textarea>

      </v-card>
      <v-btn color="primary" @click="StepOne()">Continue</v-btn>
      <v-btn text>Cancel</v-btn>
    </v-stepper-content>
    <!--Titulo y descripcion-->


    <!-- paso 2 Asignar cliente-->
    <v-stepper-step :complete="e6 > 2" step="2">Asignar Cliente</v-stepper-step>

    <v-stepper-content step="2">
      <v-card color="" class="mb-12 px-5 " height="200px">
        <v-row>

          <v-col cols="1" class="">

            <v-dialog v-model="dialog1" scrollable max-width="600px">
              <template v-slot:activator="{ on }">
                <v-btn color="primary" @click="dialog1 = true">+</v-btn>
              </template>
              <v-card>
                <v-card-title>Seleciona un cliente</v-card-title>
                <v-divider></v-divider>
                <v-card-text style="height: 300px;">
                  <!--Clientes-->
                    <v-data-table v-model="selected" :headers="headers" :items="clients" :single-select="singleSelect" :search="search" item-key="email" show-select class="elevation-1">
                      <template v-slot:top>
                        <div class="px-5">
                          <v-text-field class="text-xs-center" v-model="search" append-icon="search" label="busqueda" single-line hide-details></v-text-field>
                        </div>
                      </template>
                    </v-data-table>
                  <!--Clientes-->
                </v-card-text>
                <v-divider></v-divider>
                <v-card-actions>
                  <v-btn color="blue darken-1" text @click="dialog1 = false">Close</v-btn>
                  <v-btn color="blue darken-1" text @click="clientA()">Save</v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>

          </v-col>

          <v-col cols="11" class="text-center">

            <v-chip class="ma-2" close color="primary" text-color="white" close-icon="mdi-delete" v-if="clientBool === true " @click="EliminClient()">
              <v-avatar left>
                <v-icon>mdi-checkbox-marked-circle</v-icon>
              </v-avatar>
            {{ clientAsig.name }}
            </v-chip>

          </v-col>

        </v-row>
      </v-card>
      <v-btn color="primary" @click="stepTwo()">Continue</v-btn>
      <v-btn text>Cancel</v-btn>
    </v-stepper-content>
    <!-- paso 2 Asignar cliente-->

    <!-- paso 3 Asignar Profesionales-->
    <v-stepper-step :complete="e6 > 3" step="3">Asignar Profesionales</v-stepper-step>

    <v-stepper-content step="3">
      <v-card color="" class="mb-12" height="200px">
        <v-row>

          <v-col cols="1" class="">

            <v-dialog v-model="dialog2" scrollable max-width="600px">
              <template v-slot:activator="{ on }">
                <v-btn color="primary" @click="dialog2 = true">+</v-btn>
              </template>
              <v-card>
                <v-card-title>Seleciona un Profesional</v-card-title>
                <v-divider></v-divider>
                <v-card-text style="height: 300px;">
                  <!--Profesionales-->
                    <v-data-table v-model="selected2" :headers="headers2" :items="professionals" :single-select="singleSelect" :search="search" item-key="email" show-select class="elevation-1">
                      <template v-slot:top>
                        <div class="px-5">
                          <v-text-field class="text-xs-center" v-model="search" append-icon="search" label="busqueda" single-line hide-details></v-text-field>
                        </div>
                      </template>
                    </v-data-table>
                  <!--Profesionales-->
                </v-card-text>
                <v-divider></v-divider>
                <v-card-actions>
                  <v-btn color="blue darken-1" text @click="dialog2 = false">Close</v-btn>
                  <v-btn color="blue darken-1" text @click="professionalAsign()">Save</v-btn>
                </v-card-actions>
              </v-card>
            </v-dialog>

          </v-col>

          <v-col cols="11" class="text-center">
            <div v-if="protBool === true ">
              <v-chip class="ma-2" close color="primary" text-color="white" close-icon="mdi-delete" v-for="item of professionalAsig" :key="item.id" @click="elimPro(item)">
                <v-avatar left>
                  <v-icon>mdi-checkbox-marked-circle</v-icon>
                </v-avatar>
              {{ item.name }}
              </v-chip>
            </div>
          </v-col>

        </v-row>
      </v-card>
      <v-btn color="primary" @click="stepThre()">Continue</v-btn>
      <v-btn text>Cancel</v-btn>
    </v-stepper-content>
    <!-- paso 3 Asignar Profesionales-->

    <v-stepper-step step="4">Definir actividades para la culminación del mismo</v-stepper-step>
    <v-stepper-content step="4">
      <v-card color="" class="mb-12" height="100%">
        <v-row>
          <v-col cols="12" class="">
            <v-dialog v-model="dialog3" persistent max-width="290">

              <template v-slot:activator="{ on }">
                <v-btn color="primary" dark block v-on="on">+ Agregar Actividad</v-btn>
              </template>

              <v-card>
                <v-card-title class="headline">Actividad</v-card-title>
                <v-card-text>
                  <div class="px-5">
                    <v-text-field label="Titulo" v-model="activity.title"></v-text-field>
                  </div>
                  <div class="px-5">
                    <v-text-field label="Porcetaje representativo" type="number"  v-model="activity.percentage"></v-text-field>
                  </div>
                  <div class="px-5">
                    <v-textarea label="Descripción" v-model="activity.description"></v-textarea>
                  </div>
                </v-card-text>
                <v-card-actions>
                  <v-spacer></v-spacer>
                  <v-btn color="primary" text @click="dialog3 = false">Cerrar</v-btn>
                  <v-btn color="primary" text @click="activityA()">Agregar</v-btn>
                </v-card-actions>
              </v-card>

            </v-dialog>
          </v-col>
          <v-col cols="12">
            <v-row class="px-2"> 
              <v-col cols="4" v-for="item of activitys" :key="item.id">
                <v-card v-if="ActiBool === true">
                  <v-card-text>
                    <div class="p-1 text-center">
                      <h4> {{ item.title }} </h4>
                    </div>
                    <div class="p-2 text-center">
                      <v-tooltip left>
                        <template v-slot:activator="{ on }">
                          <v-btn color="primary" text v-on="on">Descripción</v-btn>
                        </template>
                        <span>
                          {{ item.description }}
                        </span>
                      </v-tooltip>
                    </div>
                    <div class="p-2">
                      <v-progress-linear color="primary" rounded value="0" height="13">0%</v-progress-linear>
                    </div>
                    <div class="text-right">
                      <v-btn color="primary" block @click="elimActivity(item)">Eliminar</v-btn>
                    </div>
                  </v-card-text>
                </v-card>
              </v-col>
            </v-row>
          </v-col>
        </v-row>
      </v-card>
      <v-btn color="primary" @click="stepFor()">Continue</v-btn>
      <v-btn text>Cancel</v-btn>
    </v-stepper-content>
  </v-stepper>
</template>

<script>
import { mapMutations } from 'vuex';
export default {
    name: 'ProyectosD',
    data() {
        return {
            e6: 1,
            alertMsg: '',
            alert: false,
            alertSM: '',
            alertS: false,
            dialog: false,
            dialog1: false,
            dialog2: false,
            dialog3: false,
            singleSelect: true,
            clientBool: false,
            protBool: false,
            ActiBool: false,
            clientCount: 0,
            professionalCount: 0,
            activitysCount: 0,
            search: '',
            selected: [],
            selected2: [],
            clientAsig: {
              name: '',
              email: ''
            },
            activity: {
              title: '',
              description: '',
              percentage: 0
            },
            professionalAsig: [],
            professionalArray: [],
            idProyects: [],
            proyecto: [],
            headers: [
              {
                text: 'Nombre',
                align: 'left',
                sortable: false,
                value: 'name',
              },
              { text: 'E-Mail', value: 'email' },
            ],
            headers2: [
              {
                text: 'Nombre',
                align: 'left',
                sortable: false,
                value: 'name',
              },
              { text: 'E-Mail', value: 'email' },
            ],
            clients: [],
            professionals: [],
            activitys: [],
            items1: {
              title: '',
              description: '',
              price: 0,
            },
            idPro: '',
        }
    },
    created(){
      this.axios.get('/clients')
        .then(res => {
          let clientsArray = res.data.clients;
          clientsArray.forEach(element => {
            this.charge(element)
          });
        })
        .catch(err => {
          console.log(err.response)
        })
      this.axios.get('/professionals')
        .then(res => {
          let professionalsArray = res.data.users;
          professionalsArray.forEach(element => {
            this.charge2(element)
          });
        })
        .catch(err => {
          console.log(err.response)
        })
    },
    watch: {
      alert: function(){
        setTimeout(this.alertState, 2000);
      },
      alertS: function(){
        setTimeout(this.alertStateS, 2000);
      },
    },
    methods: {
      ...mapMutations(['clientsAct', 'professionalAct']),
      StepOne(){
        let title = this.items1.title.replace(/ /g, "");
        let description = this.items1.description.replace(/ /g, "");
        let price = this.items1.price;
        if(title === "" || description === "" || price === 0){
          this.alertMsg = 'Todos los campos son obligatorios';
          this.alert = true;
          return false
        }else{
          this.e6 = 2;
        }
      },
      stepTwo(){
        if(this.clientCount === 0){
          this.alertMsg = 'Debe selecionar un cliente para continuar';
          this.alert = true;
          return false
        }else{
          this.e6 = 3;
          this.clientBool = false
        }
      },
      stepThre(){
        if(this.professionalCount === 0){
          this.alertMsg = 'Debes Asignar Profesionales al Proyecto';
          this.alert = false;
          return false
        }else{
          this.e6 = 4;
          this.protBool = false
        }
      },
      stepFor(){
        if(this.activitysCount === 0){
          this.alertMsg = 'Debe Ingresar al menos una actividad';
          this.alert = true;
          return false
        }else{
          this.axios.post('/proyects',{
            title: this.items1.title,
            description: this.items1.description,
            client: this.clientAsig._id,
            professionals: this.professionalArray,
            activities: this.activitys,
            price: this.items1.price,
            professionalCount: this.professionalCount,
            clientCount: this.clientCount,
            activitysCount: this.activitysCount
          })
            .then(res => {
              this.idPro = res.data.proyect._id;
              this.pushClient(this.clientAsig);
              this.items1 = {
                title: '',
                description: ''
              },
              this.clientAsig = {
              name: '',
              email: ''
            },
            this.professionalAsig.forEach(element => {
              this.pushId(element)
            });
             this.ActiBool = false;
              this.alertS = true;
              this.alertSM = 'Proyecto creado con exito';
              this.clientsAct();
            })
            .catch(err => {
              this.alertMsg = 'Estamos en mantenimiento',
              this.alert = true;
            })
        }
      },
      alertState(){
        this.alert = false;
      },
      alertStateS(){
        this.alertS = false;
      },
      clientA(){
        if(this.selected.length === 0){
          this.alertMsg = 'Debe selecionar Un cliente';
          this.alert = true;
          return this.dialog1 = false;
        }else{
          if(this.selected[0].asig === true){
            this.alertMsg = 'Este cliente ya posee un proyecto asignado';
            this.alert = true;
            return false
          }else{
            this.selected[0].asig = true;
            this.clientAsig = this.selected[0];
            this.clientBool = true;
            this.clientCount = 1;
            this.dialog1 = false;
          }
        }
      },
      EliminClient(){
        if(confirm('¿Estas seguro de eliminar este cliente? Todo proyecto necesita un cliente...')){
          this.clientAsig = '';
          this.clientBool = false;
          this.clientCount = 0;   
        }
      },
      charge(element){
        this.clients.push(element)
      },
      charge2(element){
        this.professionals.push(element)
      },
      professionalAsign(){
        if(this.selected2.length === 0){
          this.alertMsg = 'Debes seleccionar por lo menos a un profesional';
          this.alert = true;
          return this.dialog2 = false
        }else{
          if(this.selected2[0].role === 'admin'){
            this.alertMsg = 'El administrador no puede ser seleccionado para un proyecto';
            this.alert = true;
          }else{
            this.protBool = true;
            this.professionalAsig.push(this.selected2[0]);
            this.professionalArray.push(this.selected2[0]._id);
            this.professionalCount++;
            this.dialog2 = false;
          }
        }
      },
      elimPro(item){
        if(confirm('¿Estas seguro de eliminar este Profesional?')){
          let index = this.professionalAsig.indexOf(item);
          this.professionalAsig.splice(index, 1);
          this.professionalCount --;
        }
      },
      activityA(){
        let title = this.activity.title.replace(/ /g, "");
        let description = this.activity.description.replace(/ /g, "");
        if(title === '' || description === '' || this.activity.percentage === 0){
          this.alertMsg = 'todos los campos son obligatorios';
          this.alert = true;
          this.activity.title = '';
          this.activity.description = '';
          return this.dialog3 = false
        }else{
          this.ActiBool = true;
          this.activitys.push(this.activity);
          this.activity = {
            title: '',
            descripcion: '',
            percentage: 0
          }
          this.activitysCount++;
          this.dialog3 = false;
        }
      },
      elimActivity(item){
        if(confirm('¿Estas seguro de eliminar esta Actividad?')){
          let index = this.activitys.indexOf(item);
          this.activitys.splice(index,1);
          this.activitysCount--;
        }
      },
      pushId(item){
        this.axios.get('/professionals')
          .then(res => {
            res.data.users.forEach(element => {
              if(element.email === item.email){
                this.idProyects = element.proyects;
                this.idProyects.push(this.idPro)
                this.axios.put(`professionals/${element.email}`,{
                  name: element.name,
                  lastName: element.lastName,
                  email: element.email,
                  profesion: element.profesion,
                  role: element.role,
                  asig: true,
                  proyects: this.idProyects
                })
                if(element.asig === false){
                  this.professionalAct();
                }
              }
            });
          })
          .catch(err => {
            console.log(err.response)
          })
      },
      pushClient(item){
            this.axios.put(`clients/${item.email}`, item)
              .then(res => {
                console.log('exito')
              })
              .catch(err => {
              })
      }
    },
}
</script>