<template>
  <v-data-table :headers="headers" :search="search" :items="clients" sort-by="calories" class="elevation-1">
    <template v-slot:top>
      <v-toolbar flat color="white">

        <v-toolbar-title>
          <span> Clientes</span>
        </v-toolbar-title>

        <v-divider class="mx-4" inset vertical></v-divider>

        <v-alert dense text type="success" v-if="alertState === true">
          {{ alertMsg }}
        </v-alert>

        <v-alert dense text type="error" v-if="alertStateE === true">
          {{ alertMsg }}
        </v-alert>

        <v-spacer></v-spacer>
      
        <v-text-field class="text-xs-center" v-model="search" append-icon="search" label="busqueda" single-line hide-details></v-text-field>
        <v-spacer></v-spacer>
        <v-dialog v-model="dialog" max-width="500px">

          <template v-slot:activator="{ on }">
            <v-btn color="primary" dark class="mb-2" v-on="on">Agregar Cliente</v-btn>
          </template>
          <v-card>

            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.name" label="Nombre"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.lastName" label="Apellido"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.email" label="E-Mail"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.phone" label="Telefono"></v-text-field>
                  </v-col>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field v-model="editedItem.direction" label="Dirección"></v-text-field>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
              <v-btn color="blue darken-1" text @click="save">Save</v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>

      </v-toolbar>

    </template>

    <template v-slot:item.action="{ item }">
      <v-icon small class="mr-2" @click="editItem(item)">edit</v-icon>
      <v-icon small @click="deleteItem(item)" >delete
      </v-icon>
    </template>

  </v-data-table>
</template>


<script>
import { mapState, mapMutations } from 'vuex';

  export default {
    name: 'Clients',
    data: () => ({
      dialog: false,
      headers: [
        {
          text: 'Nombre',
          align: 'left',
          sortable: false,
          value: 'name',
        },
        { text: 'Apellido', value: 'lastName' },
        { text: 'E-Mail', value: 'email' },
        { text: 'Telefono', value: 'phone' },
        { text: 'Dirección', value: 'direction' },
        { text: 'Acciones', value: 'action', sortable: false },
      ],
      clients: [],
      editedIndex: -1,
      alertState: false,
      alertStateE: false,
      alertMsg: '',
      search: '',
      editedItem: {
        name: '',
        lastName: '',
        email: '',
        phone: '',
        direction: '',
      },
      defaultItem: {
        name: '',
        lastName: '',
        email: '',
        phone: '',
        direction: '',
      },
    }),

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'Agregar Cliente' : 'Editar Cliente'
      },
      ...mapState(['clientsState'])
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
      alertState: function(){
        setTimeout(this.alertStateF, 3000);
      },
      alertStateE: function(){
        setTimeout(this.alertStateError, 3000);
      },
    },

    created () {
      this.initialize();
    },

    methods: {
      ...mapMutations(['clientsWatchPlus', 'clientsWatchLess']),
      alertStateF(){
        this.alertState = false;
      },
      alertStateError(){
        this.alertStateE = false;
      },
      initialize () {
        this.axios.get('/clients')
          .then(res => {
            let ClientsArray = res.data.clients;
            ClientsArray.forEach(element => {
              this.charge(element);
            });
          })
          .catch(err => {
            console.log(err.response)
          })
      },
      charge(element){
        this.clients.push(element);

      },
      editItem (item) {
        this.editedIndex = this.clients.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        const index = this.clients.indexOf(item)
        if(confirm('¿Estas seguro de eliminar este cliente?')){
          if(item.asig === true){
            this.alertStateE = true;
            this.alertMsg = 'Este cliente posee un proyecto asignado, no puede eliminarlo';
          }else{
            this.axios.delete(`clients/${item.email}`)
              .then(res => {
                this.clients.splice(index, 1);
                this.alertState = true;
                this.alertMsg = res.data.msg;
                this.clientsWatchLess();
              })
              .catch(err => {
                this.alertStateE = true;
                this.alertMsg = 'Ocurrio un error inesperado';
              })
          }
        }
      },

      close () {
        this.dialog = false
        setTimeout(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        }, 300)
      },
      save () {
        if (this.editedIndex > -1) {
          let fieldsValidate = this.validate(this.editedItem);
          if(fieldsValidate === true){
            let email = this.editedItem.email;
            this.axios.put(`clients/${this.editedItem.email}`,{
              name: this.editedItem.name,
              lastName: this.editedItem.lastName,
              email: this.editedItem.email,
              phone: this.editedItem.phone,
              direction: this.editedItem.direction
            })
              .then(res => {
                Object.assign(this.clients[this.editedIndex], this.editedItem);
                this.alertState = true;
                this.alertMsg = res.data.msg;
              })
              .catch(err => {
                this.alertStateE = true;
                this.alertMsg = 'No puedes modificar el E-Mail de contacto';
              })
          }else{
            this.alertStateE = true;
            this.alertMsg = 'Los campos no pueden estar vacios';
            this.close;
          }
        } else {
          let validateFields = this.validate(this.editedItem);
          if(validateFields === true){
            this.axios.post('/signinClients',{
              name: this.editedItem.name,
              lastName: this.editedItem.lastName,
              email: this.editedItem.email,
              phone: this.editedItem.phone,
              direction: this.editedItem.direction,
            })
              .then(res => {
                this.clients.push(this.editedItem);
                this.alertState = true;
                this.alertMsg = res.data.msg;
                this.clientsWatchPlus();
              })
              .catch(err => {
                if(err.response.data.error.errors.email.message){
                  this.alertStateE = true;
                  this.alertMsg = 'Por favor ingrese un E-Mail Valido';
                }
              })
            this.close();
          }else{
            this.alertStateE = true;
            this.alertMsg = 'Todos los campos son obigatorios';
          }
        }
      },
      validate(item){
        let name = item.name.replace(/ /g, "");
        let lastName = item.lastName.replace(/ /g, "");
        let email = item.email.replace(/ /g, "");
        let phone = item.phone.replace(/ /g, "");
        let direction = item.direction.replace(/ /g, "");
        if(name === "" || lastName === "" || email === "" || phone === "" || direction === ""){
          return false;
        }else{
          return true;
        }
      },
    },
  }
</script>