<template>
    <v-container fluid>
        <v-row>
            <v-col cols="12" class="" v-for="item of proyects" :key="item.id">
                <v-alert dense text type="error" v-if="alertStateE === true">
                {{ alertMsg }}
                </v-alert>
                <v-card class="mx-auto" max-width="100%">
                    <v-card-text>
                        <h2> {{ item.title }} </h2>
                        <p>
                            {{ item.description }}
                        </p>
                        <p>
                            Progeso: 
                            <v-progress-linear color="primary" height="13" rounded v-model="item.percentage">
                                {{ item.percentage }}%
                            </v-progress-linear>
                        </p>
                        <v-card class="mx-auto" max-width="100%" v-for="act of item.activities" :key="act.id">
                            <v-card-text>
                                <h4> {{act.title}} </h4>
                                <p>
                                   {{ act.description }}
                                </p>
                                <p>
                                    <v-progress-linear color="primary" height="13" rounded v-if="act.state === false">
                                        0%
                                    </v-progress-linear>
                                    <v-progress-linear color="primary" height="13" rounded v-if="act.state === true" value="100">
                                        100%
                                    </v-progress-linear>
                                </p>
                                <v-checkbox v-model="act.state" :label="`Terminar tarea:`"></v-checkbox>
                            </v-card-text>
                        </v-card>
                    </v-card-text>
                    <div class="text-right" v-if="item.finish === false">
                        <v-btn color="primary" class="m-3" @click="proyect(item)">Actualizar Proyecto</v-btn>
                        <v-btn color="primary" class="m-3" @click="finProyect(item)">Terminar Proyecto</v-btn>
                    </div>
                </v-card>
            </v-col>
        </v-row>
    </v-container>
</template>

<script>
export default {
    name: 'DashboardU',
    data() {
        return {
            proyects: [],
            activities: [],
            alertStateE: false,
            alertMsg: '',
        }
    },
    created() {
        let UserData = this.$session.getAll();
        this.axios.get('/proyects')
            .then(res => {
                res.data.data.forEach(element => {
                   element.professionals.forEach(element2 => {
                       if(element2.email === UserData.true.email){
                           this.proyects.push(element) 
                       }
                   });
                });
            })
            .catch(err => {
                console.log(err.response)
            })
    },
    watch: {
        proyects: function(){
            if(this.proyects.length === 0){
                let user = this.$session.getAll();
                user.true.asig = false;
                this.axios.put(`professionals/${user.true.email}`, user.true)
                .then(res => {

                })
                .catch(err => {
                    console.log(err.response)
                })
            }
        },
        alertStateE: function(){
            setTimeout(() =>{
                this.alertStateE = false;
            },2000)
        }
    },
    methods: {
        proyect(item){
            let progress = 0;
            this.activities = item.activities;
            this.activities.forEach(element => {
                if(element.state === true){
                    progress += element.percentage;
                }
            });
            item.percentage = progress;
            this.axios.put(`proyects/${item._id}`, item)
                .then(res => {
                    
                })
                .catch(err => {
                    console.log(err.response)
                })
        },
        finProyect(item){
            if(item.percentage === 100){
                let index = this.proyects.indexOf(item);
                console.log(index)
                item.finish = true;
                this.axios.put(`proyects/${item._id}`, item)
                .then(res => {
                    this.proyects.splice(index, 1);
                })
                .catch(err => {
                    console.log(err.response)
                })
            }else{
                this.alertStateE = true;
                this.alertMsg = 'el proyecto debe estar completado al 100%';
            }
        },
    },
}
</script>